
<template>
  <div id="amap-page-container">
    <div id="searchStyle">
      <!--搜索框-->
      <van-search
        id="input"
        placeholder="请输入地址"
        use-action-slot="true"
        v-model="obj.address"
        @keyup="getAddress"
      >
        <view slot="action" bindtouchstart="onSearch">搜索</view>
      </van-search>
    </div>
    <!--地图-->
    <div id="amap-container">
      <el-amap
        ref="map"
        vid="amapDemo"
        :zoom="zoom"
        :center="mapCenter"
        class="amap-demo"
        :plugin="plugin"
        :events="events"
      >
        <el-amap-marker
          v-for="(marker, index) in markers"
          :position="marker"
          :key="index"
        ></el-amap-marker>
      </el-amap>
      <!--确定按钮-->
      <van-row>
        <van-col span="16" offset="4">
          <van-button type="info" @click="confirm" block>确定</van-button>
        </van-col>
      </van-row>
    </div>
  </div>
</template>
<script>
//import "../../css/map.less";
export default {
  name: "app",
  data() {
    let self = this;
    return {
      layout: {
        search_div_height: 54,
      },
      obj: {
        address: "",
        lngLat: [],
      },
      zoom: 16,
      markers: [],
      searchOption: {
        city: "全国",
        citylimit: true,
      },
      loaded: false,
      mapCenter: [121.59996, 31.197646],
      plugin: [
        {
          enableHighAccuracy: true, //是否使用高精度定位，默认:true
          timeout: 100, //超过10秒后停止定位，默认：无穷大
          maximumAge: 0, //定位结果缓存0毫秒，默认：0
          convert: true, //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
          showButton: true, //显示定位按钮，默认：true
          buttonPosition: "RB", //定位按钮停靠位置，默认：'LB'，左下角
          showMarker: true, //定位成功后在定位到的位置显示点标记，默认：true
          showCircle: false, //定位成功后用圆圈表示定位精度范围，默认：true   //苹果有效
          panToLocation: true, //定位成功后将定位到的位置作为地图中心点，默认：true
          zoomToAccuracy: true, //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：f
          extensions: "all",
          pName: "Geolocation",
          events: {
            init(instance) {
              //定位成功 自动将circle移到定位点
              instance.getCurrentPosition((status, result) => {
                console.log(result.position.lng);
                console.log(result.position.lat);
                //console.log(result);
                if (result && result.position) {
                  self.center = [result.position.lng, result.position.lat];
                  self.loaded = true;
                } else {
                  console.log(`定位失败`);
                }
              });
              console.log(instance);
            },
          },
        },
        {
          //地图工具条
          pName: "ToolBar",
        },
        {
          //左下角缩略图插件 比例尺
          pName: "Scale",
        },
      ],
      events: {
        click(e) {
          self.markers = [];
          let { lng, lat } = e.lnglat;
          self.lng = lng;
          self.lat = lat;
          self.markers.push([self.lng, self.lat]);
          self.obj.lngLat = [self.lng, self.lat];
          // 这里通过高德 SDK 完成。
          var geocoder = new AMap.Geocoder({
            radius: 1000,
            extensions: "all",
          });
          geocoder.getAddress([lng, lat], function (status, result) {
            if (status === "complete" && result.info === "OK") {
              if (result && result.regeocode) {
                self.obj.address = result.regeocode.formattedAddress;
                self.$nextTick();
              }
            }
          });
        },
      },
    };
  },
  methods: {
    confirm() {
      var _this = this;
      if (_this.obj.address != "") {
        _this.$router.push("/publishTask");
        localStorage.setItem("LngLat", JSON.stringify(_this.obj.lngLat));
        localStorage.setItem("address", _this.obj.address);
      } else {
        this.toastError("请您选择任务地址");
      }
    },
    getAddress() {
      var _this = this;
      AMap.plugin(["AMap.Autocomplete", "AMap.PlaceSearch"], function () {
        var autoOptions = {
          input: "input",
        };
        var autoComplete = new AMap.Autocomplete(autoOptions); // 无需再手动执行search方法，autoComplete会根据传入input对应的DOM动态触发search
        AMap.event.addListener(autoComplete, "select", function (data) {
          console.log("输出");
          console.log(data);
          _this.obj.address = data.poi.name;
          _this.markers = [];
          _this.obj.lngLat = [];
          _this.mapCenter = [data.poi.location.lng, data.poi.location.lat];
          _this.obj.lngLat = [data.poi.location.lng, data.poi.location.lat];
          _this.markers.push([data.poi.location.lng, data.poi.location.lat]);
        });
      });
    },
    getSession() {
      var LngLat = JSON.parse(localStorage.getItem("LngLat")); //获取地理坐标
      var address = localStorage.getItem("address");
      console.log(LngLat);
      if (LngLat != null && LngLat.length > 0) {
        this.markers = [];
        this.markers.push([LngLat[0], LngLat[1]]);
      }
      if (address != null) {
        //获取地址
        this.obj.address = address;
      }
    },
  },
  created() {},
  mounted() {
    this.getSession();
  },
};
</script>

<style scoped>
#amap-page-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}
#amap-container {
  -webkit-flex: 1;
  flex: 1;
}
.van-row {
  position: fixed;
  bottom: 20px;
  z-index: 2;
  width: 70%;
  left: 15%;
}
.amap-touch-toolbar .amap-zoomcontrol {
  bottom: -20px !important;
}
</style>

